.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 10px;
  row-gap: 36px;
}

#typeOfDataDisplay {
  margin: 15px 10px 0px;
}

.grid-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
.heart {
  position: absolute;
  z-index: 20;
  right: 10px;
  top: 10px;
}
.grid-item:hover {
  cursor: pointer;
}
.nameRegionImage {
  display: flex;
  position: relative;
}
.grid-item img {
   width: 100%; 
   height:100%;
  object-fit: cover;
  border-radius: 5px;
  max-height: 320px;
  transition: transform 0.3s ease;
  &:hover {
    transform: scale(1.1); 
  }
}

.name {
  padding: 5px;
  border-radius: 5px;
  font-size: 15px;
  font-weight: 600;
}
.detialsContainer {
  position: relative;
}
.nameRegion {
  position: absolute;
  bottom: 5px;
  color: white;
}
.pricing {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 20px;
  color: white;
  font-weight: bold;

  display: flex;
}

.fieldContainer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.fields {
  font-size: 12px;
  background-color: rgb(207, 207, 207);
  margin: 2px;
  padding: 3px;
  border-radius: 5px;
}
